import React from 'react';

const SearchBox = ({searchfield, searchChange}) => {
	return (
		<div className='pa2'>
		<input 
		className='pa3 ba b--green bg-lightest-blue br3 tc'
		type='search' 
		placeholder ='Friends Name'
		onChange = {searchChange} 
		/>
		</div> 
	)
}

export default SearchBox;